<template>
	<div class="card_list">
		<table class='table'>
			<tbody>
				<tr>
					<td width="12%">卡片名称:</td>
					<td width="12%"><input type="text" class="form-control" v-model="name_cont" /></td>
					<td width="12%">卡片号:</td>
					<td width="12%"><input type="text" class="form-control" v-model="card_code_cont" /></td>
					<td width="12%">卡片内容:</td>
					<td width="12%"><input type="text" class="form-control" v-model="content_cont"></td>
					<td width="12%">特征:</td>
					<td width="12%"><input type="text" class="form-control" v-model="feature_cont"></td>
				</tr>
				<tr>
					<td>产品名:</td>
					<td colspan="3">
						<select name="product_id_eq" v-model="product_id_eq">
							<option value></option>
							<template v-for="product in products">
								<option :value="product.id">{{product.name}}({{product.raw_name.substring(0,30)}})</option>
							</template>
						</select>
					</td>
					<td>新产品名:</td>
					<td colspan="3">
						<select name="new_product_id_eq" v-model="new_product_id_eq">
							<option value></option>
							<template v-for="new_product in new_products">
								<option :value="new_product.id">{{new_product.name}}({{new_product.raw_name.substring(0,30)}})</option>
							</template>
						</select>
					</td>
				</tr>
				<tr>
					<td>触发器:</td>
					<td colspan="3">
						<select name="triggers_id_in" multiple size="5" style="width: 80%" v-model="triggers_id_in">
							<option value></option>
							<template v-for="trigger in triggers">
								<option :value="trigger.id">{{trigger.name}}({{trigger.raw_name.substring(0,15)}})</option>
							</template>
						</select>
					</td>
					<td>攻击力>=</td>
					<td><input type="text" v-model="attack_value_gteq"></td>
					<td>攻击力<=</td>
					<td><input type="text" v-model="attack_value_lteq"></td>
				</tr>
				<tr>
					<td>等级>=</td>
					<td><input type="text" v-model="level_gteq"></td>
					<td>等级<=</td>
					<td><input type="text" v-model="level_lteq"></td>
					<td>魂>=</td>
					<td><input type="text" v-model="soul_gteq"></td>
					<td>魂<=</td>
					<td><input type="text" v-model="soul_lteq"></td>
				</tr>
				<tr>
					<td>cost>=</td>
					<td><input type="text" v-model="cost_gteq"></td>
					<td>cost<=</td>
					<td><input type="text" v-model="cost_lteq"></td>
				</tr>
				<tr>
					<td colspan="8">
						<div class="pull-right">
							<input type="button" value="搜索" class="btn btn-primary" @click="search">
							<input type="button" value="清空" class="btn btn-default" @click="clear_search">
						</div>
					</td>
				</tr>
			</tbody>
		</table>
	
		<paginate
			:page-count="total_pages"
			:page-range="3"
			ref="paginate"
			:margin-pages="2"
			:click-handler="clickCallback"
			:prev-text="'上一页'"
			:next-text="'下一页'"
			:container-class="'pagination'"
			:page-class="'page-item'">
		</paginate>
		<span>共计{{this.total_count}}张</span>
		<span v-show="status=='loading'">正在加载数据...</span>
		<table width="80%" class="table table-condensed table-hover table-striped" >
			<thead style="text-align: center; vertical-align: center">
				<tr>
					<td>卡片</td>
					<td>卡名</td>
					<td>效果</td>
			</tr>
			</thead>
			<tbody>
				<template v-for="card in cards">
					<tr>
						<td>
							<img v-bind:src="host + card.picture.http_url" />
						</td>
						<td>
							<router-link :to="{name: 'CardDetail', params: {id: card.id}}">{{card.name}}</router-link>
						</td>
						<td>
							<router-link :to="{name: 'CardDetail', params: {id: card.id}}">{{card.content}}</router-link>
						</td>
						
					</tr>	  
				</template>
			</tbody>
		</table>
	</div>
</template>

<script>
	import Vue from 'vue'
	import Paginate from 'vuejs-paginate'
	Vue.component('paginate', Paginate)

    export default {
        name: 'CardList',
        data () {
            return {
                status: 'loading',
                cards: [],
				page: 1,
				total_pages: 20,
				total_count: 0,
				host: "http://xiayuanyin.cn:4002",
				products: [],
				new_products: [],
				triggers: [],
				//以下搜索相关
				name_cont: "",
				card_code_cont: "",
				content_cont: "",
				feature_cont: "",
				product_id_eq: "",
				new_product_id_eq: "",
				triggers_id_in: [],
				attack_value_gteq: "",
				attack_value_lteq: "",
				soul_gteq: "",
				soul_lteq: "",
				level_gteq: "",
				level_lteq: "",
				cost_gteq: "",
				cost_lteq: ""
            }
        },

        methods: {
			clickCallback(pageNum) {
				this.$refs.paginate.selected = pageNum - 1
				this.page = pageNum
				this.get_cards()
			},

			search() {
				this.page = 1
				this.$refs.paginate.selected = 0
				this.get_cards()
			},

			clear_search() {
				this.name_cont = "";
				this.card_code_cont = "";
				this.content_cont = "";
				this.feature_cont = "";
				this.product_id_eq ="";
				this.new_product_id_eq= "";
				this.triggers_id_in =[];
				this.attack_value_gteq= "";
				this.attack_value_lteq= "";
				this.soul_gteq="";
				this.soul_lteq="";
				this.level_gteq= "";
				this.level_lteq= "";
				this.cost_gteq= "";
				this.cost_lteq= ""
			},

			do_fetch(url) {
				return(
					fetch(url, {
						method: "GET",
						headers: {
								'Accept': 'application/json'
						}
					})
					.then((responseData) => {return responseData.json()})
				)
			},

            get_cards() {
                this.status = 'loading'
                let url = "http://xiayuanyin.cn:4002/api/v1/cards?"
				//let url = "http://localhost:3000/api/v1/cards"
				url += `&page=${this.page}`
				var cols = [
							'name_cont', 'card_code_cont', 'content_cont', 'feature_cont', 'product_id_eq', 'new_product_id_eq', 'triggers_id_in', 
							'attack_value_gteq', 'attack_value_lteq', 'soul_gteq', 'soul_lteq', 'level_gteq', 'level_lteq', 'cost_gteq', 'cost_lteq'
							
						]
				cols.forEach((col) => {
					url += `&${col}=${encodeURI(this[col])}`
				})
				console.log(this.triggers_id_in)
				console.log(url)
                this.do_fetch(url).then((data) => {
					this.cards = data.cards
					this.total_pages = data.meta.total_pages
					this.total_count = data.meta.total_count
					this.status = 'loaded'
                })
			},
			
			get_products() {
				let url = this.host + "/api/v1/products"
				this.do_fetch(url).then((data) => {
					this.products = data.products
				})

			},

			get_new_products() {
				let url = this.host + "/api/v1/new_products"
				this.do_fetch(url).then((data) => {
					this.new_products = data.new_products
				})
			},

			get_triggers() {
				let url = this.host + "/api/v1/triggers"
				this.do_fetch(url).then((data) => {
					this.triggers = data.triggers
				})
			}
        },

        created () {
			this.get_cards()
			this.get_products()
			this.get_new_products()
			this.get_triggers()
		}

		
    }
</script>




