import Vue from 'vue'
import Router from 'vue-router'
import CardList from '@/components/CardList'
import CardDetail from '@/components/CardDetail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'CardList',
      component: CardList,
      meta: { keepAlive: true }
    },
    {
      path: '/cards/:id',
      name: 'CardDetail',
      component: CardDetail
    }
  ]
})



